.footer{
  background-color: #000;
  padding: 50px 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
}

.warning{
  color: #bbb;
  font-size: 10px;
  margin: 0;
}

.links{
  color: #FFF;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px
}

.link{
  text-decoration: none;
  font-size: 15px;
  color: #FFF;
}

@media screen and (max-width: 600px) {
  .warning{
    text-align: center;
  }
}