.cart {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
}

.cart_empty{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 80vh;
  flex-direction: column;
}

.cart_empty .title{
  color: #bbb;
}

.cart_empty .link{
  color: #bbb;
  font-size: 20px;
  transition: ease-in-out .3s color;
}

.cart_empty .link:hover{
  color: #000;
}

.link{
  text-decoration: none;
  color: #000;
  cursor: pointer;
}

.title {
  font-size: 50px;
  margin: 30px 0;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 80%;
  gap: 20px;
  flex-direction: column;
}

.card {
  display: flex;
  width: 100%;
  background-color: rgba(211, 211, 211, .1);
  border-radius: 10px;
  align-items: center;
  padding: 15px 30px;
  box-sizing: border-box;
  justify-content: space-between;
  font-size: 20px;
}

.image {
  width: 100px;
  height: 120px;
  border-radius: 5px;
  object-position: center;
  object-fit: cover;
}

.name {
  width: 20vw;
  text-align: center;
}

.group {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  width: 10vw;
}

.price {
  width: 10vw;
  text-align: center;
}

.icon {
  border: 2px solid #bbb;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: .3s ease-in-out border-color;
}

.icon:hover {
  border-color: rgba(0, 0, 0, .7);
}

.icon:hover .mark{
  color: rgba(0, 0, 0, .7);
}

.mark{
  font-size: 20px;
  font-weight: 900;
  color: #bbb;
  transition: .3s ease-in-out color;
}

.info{
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0;
  padding: 0 30px;
  box-sizing: border-box;
}

.buttons{
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0;
  box-sizing: border-box;
  margin-bottom: 100px;
}

.text{
  font-size: 30px;
  font-weight: 700;
}

.button{
  font-family: inherit;
  background-color: transparent;
  border-radius: 5px;
  padding: 15px;
  font-size: 20px;
  border: none;
  cursor: pointer;
  transition: ease-in-out .3s color;
  color: #fff;
  transition: .3s ease-in-out background-color;
  background-color: #000;
  text-decoration: none;
}

.button:hover{
  background-color:  rgb(255,41,50);
}

.buttons .link{
  padding: 15px;
  font-size: 20px;
  cursor: pointer;
  color: #bbb;
  transition: ease-in-out .3s color;
}

.buttons .link:hover{
  color: #000;
}

.promo{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 80%;
  flex-wrap: wrap;
}

.promo .text{
  font-size: 20px;
  color: #bbb;
  padding: 0 0 5px 0;
  margin: 0;
  margin-right: 10px;
}

.input{
  width: 100px;
  height: 25px;
  box-sizing: border-box;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid #bbb;
  outline: none;
  font-family: inherit;
  font-size: 20px;
  padding: 0 0 5px 0;
  text-transform: uppercase;
}

.error{
  font-size: 12px;
  color: rgb(255,41,50);
  margin: 0;
  width: 100%;
  text-align: end;
}

.warning{
  color: #50C878;
  font-size: 12px;
  margin: 0 0 20px 0;
}

@media screen and (max-width: 800px) {
  .title{
    font-size: 30px;
  }

  .card{
    font-size: 12px;
    padding: 15px 10px;
  }

  .list{
    width: 95%;
  }

  .icon{
    width: 15px;
    height: 15px;
    border: none;
  }

  .mark{
    font-size: 12px;
  }

  .image{
    width: 70px;
    height: 77px;
  }

  .text{
    font-size: 15px;
  }

  .promo{
    width: 95%;
  }

  .promo .text{
    font-size: 12px;
  }

  .input{
    height: 23px;
    font-size: 12px;
  }

  .info{
    width: 95%;
    padding: 0;
  }

  .button{
    font-size: 15px;
  }

  .buttons .link{
    font-size: 15px;
  }
  
  .buttons{
    width: 95%;
  }
}