.checkout{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 70px 10px;
}

.steps{
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper{
display: flex;
align-items: center;
justify-content: center;
width: 100%;
}

.wrapper:last-child{
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.line{
  width: 100%;
  height: 2px;
  background-color: #000;
}

.step{
  width: 30px;
  height: 30px;
  border: 2px solid #000;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.step_active{
  background-color: #000;
  color: #fff;
}

.step_previous{
  background-color: #bbb;
  color: #fff;
  border-color: #bbb;
}

.step_previous + .line{
  background-color: #bbb;
}

.step_text{
  font-size: 20px;
  width: 30px;
  text-align: center;
}

.button{
  font-family: inherit;
  background-color: transparent;
  border-radius: 5px;
  padding: 15px;
  font-size: 20px;
  border: none;
  cursor: pointer;
  transition: ease-in-out .3s color;
  color: #bbb;;
  transition: .3s ease-in-out background-color;
  background-color: transparent;
  text-decoration: none;
}

.group{
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.button + .button{
  color: #000;
}

.button:hover{
  color: rgb(255,41,50)
}

.button_classic{
  font-family: inherit;
  background-color: transparent;
  border-radius: 5px;
  padding: 10px 35px;
  font-size: 20px;
  border: none;
  cursor: pointer;
  transition: ease-in-out .3s color;
  color: #fff;
  transition: .3s ease-in-out background-color;
  background-color: #000;
}

.button_classic:hover{
  background-color:  rgb(255,41,50);
}

@media screen and (max-width: 800px) {
  .steps{
    width: 90%;
  }

  .checkout{
    padding: 40px 5px;
  }
}